import globalConfig from './config';
import {
  walletIframeId,
  type CustomEventHandler,
  type ProjectConfig,
  type WalletConfig,
  type WalletOption,
} from './types';

const openTimestamp = new Date().getTime();

export function walletUrl({
  projectConfig,
  walletOptions,
  options,
  customEventHandler,
}: {
  projectConfig: ProjectConfig;
  walletOptions: WalletOption;
  options?: WalletConfig;
  customEventHandler?: CustomEventHandler;
}): string {
  const { pathName = '/', query = {} } = options || {};
  let url = `${globalConfig.env.walletUrl}${pathName}?iframeid=${walletIframeId}&${JSON.stringify(query)
    .replace(/[{}"]/g, '')
    .split(',')
    .filter((value) => !!value?.trim())
    .map((keyValue) => {
      const [key, value] = keyValue.split(':');
      return `${encodeURIComponent(key)}=${encodeURIComponent(value.trim())}`;
    })
    .join('&')}`;

  const { projectId, clientKey, appId } = projectConfig;
  const { erc4337, language, themeType, customStyle } = walletOptions;
  const topMenuType = options?.topMenuType || walletOptions?.topMenuType;

  url += `&projectConfig=${encodeURIComponent(
    Buffer.from(JSON.stringify({ projectId, clientKey, appId })).toString('base64')
  )}`;
  if (themeType) {
    url += '&theme=' + themeType;
  }
  if (language) {
    url += '&language=' + language;
  }
  if (erc4337) {
    url += `&erc4337=${encodeURIComponent(JSON.stringify(erc4337))}`;
  }
  if (topMenuType) {
    url += `&topMenuType=${topMenuType}`;
  }
  if (customEventHandler) {
    //support custom event
    if (customEventHandler.type) {
      url += `&supportCustomEvent=${customEventHandler.type}`;
    } else {
      url += '&supportCustomEvent=true';
    }
  }
  url += `&openTimestamp=${openTimestamp}`;
  if (customStyle) {
    let supportChains = customStyle.supportChains;
    if (supportChains) {
      supportChains = supportChains.map((chain) => {
        return {
          name: chain.name,
          id: chain.id,
          chainType: chain.chainType,
        };
      });
    }
    url += `&customStyle=${encodeURIComponent(
      Buffer.from(JSON.stringify({ ...customStyle, supportChains })).toString('base64')
    )}`;
  }
  console.log('Wallet url', url);
  return url;
}
