import sha256 from 'crypto-js/sha256.js';
import stringify from 'fast-json-stable-stringify';
import { v4 as uuid } from 'uuid';
import {
  IframeWalletMessageType,
  MessageType,
  walletIframeId,
  type CustomEventHandler,
  type CustomMessageType,
  type EthereumProvider,
  type SolanaWallet,
  type WalletCore,
} from './types';

interface MessageResponse<T> {
  id: string;
  messageType: MessageType | CustomMessageType;
  data: T;
}

type RpcInput = {
  id: string;
  jsonrpc: string;
  method: string;
  params?: unknown[];
  chainId?: number;
};

const walletPluginIds = [walletIframeId];

function checkMessage<T>(message: MessageResponse<T>) {
  const { id, messageType, data } = message;
  const { nonce, date, hash, iframeId } = JSON.parse(Buffer.from(id, 'base64').toString());
  const hashBody = stringify({
    state: { nonce, date, iframeId },
    messageType,
    data,
  });
  const hashValue = sha256(hashBody).toString();
  if (hashValue === hash) {
    return {
      state: { nonce, date, hash, iframeId },
      messageType,
      data,
    };
  }
}

function buildMessage(data: unknown, messageType: MessageType | CustomMessageType, nonce?: string) {
  const state = {
    nonce: nonce || uuid(),
    date: Date.now(),
  };
  const hashValue = sha256(
    stringify({
      state,
      messageType,
      data,
    })
  )
    .toString()
    .toLowerCase();
  const id = Buffer.from(
    JSON.stringify({
      ...state,
      hash: hashValue,
    })
  ).toString('base64');

  return {
    id,
    messageType,
    data,
  };
}

const handleEthereumRpc = async (data: Partial<RpcInput>, provider?: EthereumProvider) => {
  if (!provider) {
    throw {
      code: 4200,
      message: 'Wallet plugin not support EVM chains.',
    };
  }
  const result = await provider.request(data as any);
  return result;
};

const handleSolanaRpc = async (data: Partial<RpcInput>, solana?: SolanaWallet) => {
  if (!solana) {
    throw {
      code: 4200,
      message: 'Wallet plugin not support Solana chains.',
    };
  }

  if (data.method === 'solana_requestAccounts') {
    const publicAddress = solana.selectedAddress || solana.publicKey?.toBase58();
    if (!publicAddress) {
      await solana.connect();
    }
    return solana.selectedAddress || solana.publicKey?.toBase58();
  } else if (data.method === 'solana_chainId') {
    return solana.chainId;
  } else if (data.method === 'solana_signTransaction') {
    const { VersionedTransaction } = await import('@solana/web3.js');
    const result = await solana.signTransaction(
      VersionedTransaction.deserialize(Buffer.from((data.params as string[])[0], 'base64'))
    );
    return Buffer.from(result.serialize()).toString('base64');
  } else if (data.method === 'solana_signAllTransactions') {
    const { VersionedTransaction } = await import('@solana/web3.js');
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const txs = (data.params as any)[0].map((tx: string) =>
      VersionedTransaction.deserialize(Buffer.from(tx, 'base64'))
    );
    const result = await solana.signAllTransactions(txs);
    return result.map((tx) => Buffer.from(tx.serialize()).toString('base64'));
  } else if (data.method === 'solana_signAndSendTransaction') {
    const { VersionedTransaction } = await import('@solana/web3.js');
    const { signature } = await solana.signAndSendTransaction(
      VersionedTransaction.deserialize(Buffer.from((data.params as string[])[0], 'base64'))
    );
    return signature;
  } else if (data.method === 'solana_signMessage') {
    const result = await solana.signMessage(Buffer.from((data.params as string[])[0], 'base64'));
    return Buffer.from(result.signature).toString('base64');
  } else if (data.method === 'solana_switchChain') {
    if (!solana.switchChain) {
      throw {
        code: 4200,
        message: 'Solana Wallet not support switch chain.',
      };
    }
    await solana.switchChain((data.params as number[])[0]);
  }
};

export const handleRpc = async (event: MessageEvent, walletCore: WalletCore) => {
  const messageBody = checkMessage<RpcInput>(event.data?.message);
  if (messageBody) {
    if (!walletPluginIds.includes(messageBody.state.iframeId)) {
      walletPluginIds.push(messageBody.state.iframeId);
    }
    let message;
    try {
      let result;
      if (messageBody.messageType === MessageType.EthereumRpc) {
        result = await handleEthereumRpc(messageBody.data, walletCore.ethereum);
      } else {
        result = await handleSolanaRpc(messageBody.data, walletCore.solana);
      }
      message = buildMessage({ result }, messageBody.messageType, messageBody.state.nonce);
    } catch (error: any) {
      message = buildMessage(
        {
          error: {
            message: error.message || error.stack || error.details || error,
            code: error.code,
          },
        },
        messageBody.messageType,
        messageBody.state.nonce
      );
    }

    (window.document.getElementById(messageBody.state.iframeId) as HTMLIFrameElement)?.contentWindow?.postMessage(
      {
        type: IframeWalletMessageType,
        message,
      },
      '*'
    );
  } else {
    console.error('message body hash not match, ignore the message.', event.data?.message);
  }
};

export const handleCustomEvent = async (event: MessageEvent, customEventHandler?: CustomEventHandler) => {
  const messageBody = checkMessage<unknown>(event.data?.message);
  if (messageBody) {
    if (!walletPluginIds.includes(messageBody.state.iframeId)) {
      walletPluginIds.push(messageBody.state.iframeId);
    }
    let message;
    try {
      if (!customEventHandler) {
        message = buildMessage(
          {
            error: {
              code: 4200,
              message: 'Wallet plugin not support custom event.',
            },
          },
          messageBody.messageType,
          messageBody.state.nonce
        );
      } else {
        const result = await customEventHandler(messageBody.messageType as CustomMessageType, messageBody.data);
        message = buildMessage({ result }, messageBody.messageType, messageBody.state.nonce);
      }
    } catch (error: any) {
      message = buildMessage(
        {
          error: {
            message: error.message || error.stack || error.toString(),
            code: error.code,
          },
        },
        messageBody.messageType,
        messageBody.state.nonce
      );
    }

    (window.document.getElementById(messageBody.state.iframeId) as HTMLIFrameElement)?.contentWindow?.postMessage(
      {
        type: IframeWalletMessageType,
        message,
      },
      '*'
    );
  } else {
    console.error('message body hash not match, ignore the message.', event.data?.message);
  }
};

export const sendEthereumEvent = (event: string, args: unknown) => {
  console.log('wallet send ethereum event', event, args);
  const message = buildMessage({ name: event, args }, MessageType.EthereumEvent);
  for (const iframeId of walletPluginIds) {
    (window.document.getElementById(iframeId) as HTMLIFrameElement)?.contentWindow?.postMessage(
      {
        type: IframeWalletMessageType,
        message,
      },
      '*'
    );
  }
};
