import { isServer } from './utils';

type Env = {
  walletUrl: string;
};

class GlobalConfig {
  /**
   * custom wallet url
   */
  #customWalletUrl: string | undefined;

  #devEnv: Env = {
    walletUrl: 'https://wallet-debug.particle.network',
  };

  #stagingEnv: Env = {
    walletUrl: 'https://wallet-staging.particle.network',
  };

  #productionEnv: Env = {
    walletUrl: 'https://wallet.particle.network',
  };

  get version() {
    // replace when build
    return 'web_' + '2.0.8';
  }

  get env() {
    let currentEnv = this.#productionEnv;

    // @ts-ignore
    if (!isServer() && window.__PARTICLE_ENVIRONMENT__ === 'development') {
      currentEnv = this.#devEnv;
      // @ts-ignore
    } else if (!isServer() && window.__PARTICLE_ENVIRONMENT__ === 'staging') {
      currentEnv = this.#stagingEnv;
    } else {
      currentEnv = this.#productionEnv;
    }

    if (this.#customWalletUrl) {
      currentEnv.walletUrl = this.#customWalletUrl;
    }

    return currentEnv;
  }

  updateWalletUrl(url: string) {
    if (url) {
      this.#customWalletUrl = url;
    }
  }
}

const globalConfig = new GlobalConfig();
export default globalConfig;
